.catagory-container{
    display: flex;
    flex-direction: column;
}
.catagory-products{
    display: grid;
    grid-template-columns:  1fr 1fr 1fr 1fr 1fr;
    row-gap: 10px;
    column-gap: 10px;
    margin: 1% 5%;
}

.see-more{
    display: block;
    width: 90%;
    margin: 1% auto;
    padding: 1% 0%;
    font-size: 1rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: #e2e2e2;
}
.see-more:hover{
    background-color: white;
}

@media(max-width:900px){
    .catagory-products{
        grid-template-columns:  1fr 1fr 1fr 1fr;
    }
}
@media(max-width:800px){
    .catagory-products{
        grid-template-columns:  1fr 1fr 1fr;
        margin: 1%;
    }
    .catagory-container{
        flex-direction: row;
    }
}

@media(max-width:550px){
    .catagory-products{
        grid-template-columns:  1fr 1fr;
    }
    .see-more{
        width: 100%;
    }
}