.blog-container{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap: 30px;
padding: 1% 8%;
}

.blog-top{
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 20px;
    align-items: center;
}
.blog-top-2{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.blog-bottom{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0% 5%;
    margin-bottom: 6%;
}

.blog-bottom>div{
    margin-bottom: 7%;
}

@media(max-width:800px){
    .blog-top{
        grid-template-columns: 1fr;
    }
    .blog-top-2{
        flex-direction: row;
    }
}

@media(max-width:550px){
    .blog-bottom{
        grid-template-columns: 1fr 1fr;
    }
    .blog-top-2{
        gap: 5%;
    }
}