.navbar{
    width: 100%;
    padding: 5px 0px;
    display: flex;
    height: 50px;
    justify-content: space-between;
    align-items: center;
    background-color: #1a163a;
    color: white;
    position: sticky;
    top: 0px;
    z-index: 1;
}
.logo-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 10px;
}

.logo-container img{
    height: 45px;
    border-radius: 50%;
}

.logo-container p{
    font-size: 25px;
    font-weight: 600;
}

.menu {
    display: flex;
    gap: 35px;
}
.menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    list-style-type: none;
}
.menu li a{
    text-decoration: none;
    color: white;
    cursor: pointer;
}
.menu hr{
    width: 100%;
}
.profile{
    display: flex;
    align-items: center;
    gap: 25px;
    padding-right: 5px;
}
.profile svg{
    color: white;
    padding-top: 5px;
}

.profile img{
    height: 45px;
    border-radius: 50%;
}

.profile button{
    color: white;
    font-size: 15px;
    border: 1px solid white;
    padding: 5px 15px;
    background-color: #435585ab;
    border-radius: 50px;
    cursor: pointer;
}
.profile button:active{
    background-color: #818FB4;
}

.nav-dropdown{
    display: none;
}
/* ---------------------- */

@media(max-width:1024px){
    .navbar{
        width: 100%;
    }
}

@media(max-width:800px){
    .navbar{
        height: 40px;
        gap: 10px;
    }
    .nav-dropdown{
        display: block;
    }
    .logo-container{
        padding-left: 3%;
    }
    .menu{
        display: none;
        position: absolute;
        top: 50px;
        left: 0px;
        width: 100%;
        justify-content: center;
        background-color: #1a163ae3;
        padding: 5px 0px;
        transition: all 200ms;
    }
    .nav-menu-visible{
        display: flex;
    }
    .logo-container img{
        height: 35px;
    }

}
@media(max-width:550px){
    .profile{
        gap: 10px;
    }
    .profile img{
        height: 35px;
    }
    .profile button{
        font-size: 14px;
        padding: 5px 10px;
    }
}
@media(max-width:370px){
    .logo-container{
        gap: 2px;
    }
    .logo-container p{
        font-size: 20px;
        font-weight: 600;
    }
    .menu li{
        font-size: 12px;
    }
}
