.Trend{
    width: 100%;
    margin: 1.5% 0%;
    padding: 1% 0%;
    flex-wrap: nowrap;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Trend-container::-webkit-scrollbar {
    display: none;
}


.Trend h2{
    margin-left: 1%;
}

.Trend-container{
    display: flex;
    gap: 5px;
    justify-content: space-between;
    margin: 1% 0%;
    align-items: center;
    scroll-behavior: smooth;
}

.Trend>button{
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background: transparent;
    cursor: pointer;
    height: 5%;
}

@media(max-width:800px){
    .Trend>div{
        width: 87%;
    }
    .Trend-container{
        width: 100%;
        overflow-x:auto;
    }
    .Trend-container::-webkit-scrollbar {
        display: none;
    }
    .Trend-container .item{
        min-width: 25vw;
        height: 300px;
    }
    .Trend-container .item-img-container{
        min-height: 120px;
    }

}

@media(max-width:550px){
    .Trend>div{
        width: 79%;
    }
    .Trend-container{
        width: 100%;
        overflow-x:auto;
    }
    .Trend-container .item{
        min-width: 30vw;
        height: 300px;
    }
    .Trend-container .item-img-container{
        min-height: 120px;
    }

    .Trend h2{
        margin-left: 0%;
        margin-top: 3%;
    }
}