.circle-trend{
    display: flex;
    align-items: center;
    gap: 5%;
    justify-content: center;
    margin: 2% 1%;
    background-color: rgb(217 217 217 / 19%);
    padding: 2% 0%;
}
.circle-trend-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 5rem;
    height: 5rem;
    background-color: rgb(255, 255, 255);
    border: 2px dashed red;
    border-radius: 50%;
}
.circle-trend>div{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle-trend img{
    object-fit: cover;
    width: 57%;
}

@media(max-width:800px){
    .circle-trend-item{
        width: 4rem;
        height: 4rem;
    }
    .circle-trend img{
        width: 36px;
    }
    .circle-trend p{
        font-size: 13px;
    }
}
@media(max-width:550px){
    .circle-trend{
        gap: 1px;
        margin: 2% 0%;
        justify-content: space-around;
    }
    .circle-trend-item{
        width: 4rem;
        height: 4rem;
    }
    .circle-trend img{
        width: 36px;
    }
    .circle-trend p{
        font-size: 13px;
    }
}

