.catagoriesmenu {
    background-color: #1a163ad8;
    padding: 8px;
}

.catagoriesmenu ul {
    display: flex;
    gap: 10px;
}

.catagoriesmenu li {
    list-style-type: none;
    color: white;
    padding: 5px;
    transition: all 100ms;
}

.catagoriesmenu li:hover {
    background-color: white;
    color: black;
}

.catagoriesmenu a {
    text-decoration: none;
}

@media(max-width:800px) {
    .catagoriesmenu {
        z-index: 0;
        border-bottom-right-radius: 5px;
    }

    .catagoriesmenu>ul {
        position: sticky;
        top: 50px;
        flex-direction: column;
        margin-top: 40px;
        padding: 5px 0px;
        border-top: 2px solid white;
    }
}

@media(max-width:550px) {
    .catagoriesmenu {
        padding: 0px;
        font-size: 15px;
    }
}