.login-container{
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    margin: 20px auto;
    background-color: white;
}
.input-fields{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    width: 100%;
}
.input-fields>input{
    width: 90%;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #cdcdcd;
}
.login-bottom{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.login-btn{
    width: 100%;
    background-color: #ef4242;
    color: white;
    border: 1px solid black;
    border-radius: 2px;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
}
.privacy-policy{
    margin-top: 5px;
    display: flex;
    gap: 5px;
}

@media(max-width:800px){
    .login-container{
        width: 50%;
    }
}
@media(max-width:800px){
    .login-container{
        width: 70%;
    }
    .login-bottom{
        font-size: 13px;
    }
}
