.slide-container{
    display: flex;
    width: 100%;
    height: 18vw;
    justify-content: space-between;
    align-items: center;
    background-size: cover;
}
.slide-container button{
    height: 45%;
    margin: 0.5%;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    background-color: #f5e8c782;
}

.slide-container>div{
    width: 100%;
    height: 18vw;
    background-size: cover;
    position: absolute;
    left: 0px;
    z-index: -1;
}

@media(max-width:1024px){
    .slide-container{
        height: 20vw;
        margin-top: 1%;
    }
    .slide-container>div{
        height: 20vw;
    }
}

@media(max-width:550px){
    .slide-container{
        height: 50vw;
        margin-top: 1%;
        overflow:hidden;
    }
    .slide-container>div{
        height: 50vw;
        background-position: center;
    }
}