.item{
    min-width: 15vw;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 1.3%;
    border-radius: 5px;
    min-height: 350px;
    justify-content: space-between;
}
.item-discount{
    font-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    background-color: #ff0000;
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
.item-img-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 153px;

}
.item img{
    width: 80%;
    /* min-width: 160px;
    max-width: 160px; */
    max-height: 190px;
    padding-bottom: 10px;
    object-fit: cover;
}

.product_name{
    font-size: 130%;
    line-height: 25px;
    padding: 10px 0px;
    color: #435585;
    font-family: 'Oswald', sans-serif;
    height: 60px;
}

.item-bottom{
    width: 100%;
    display: flex;
    gap: 10px;
    justify-content: space-between;
}

.item-bottom button{
    background-color: #433a8a;
    border: none;
    color: white;
    border-radius: 3px;
    transition:100ms all;
}
.item-bottom button:hover{
    background-color: #ef4242;
}
.item-bottom button a{
    text-decoration: none;
    color: white;
    cursor: pointer;
    padding: 10px 5px;
    font-size:100%;
}

.old_price{
    text-decoration: line-through;
    font-family: 'Roboto', sans-serif;
    font-size: 100%;
}

.new_price{
    color: red;
    font-family: 'Roboto', sans-serif;
    font-size: 130%;
}

@media(max-width:1014px){
    .item{
        padding: 8px;
        max-height: 322px;
    }
    .product_name{
        font-size: 110%;
        padding: 0px;
    }
    .old_price{
        font-size: 85%;
    }
    .new_price{
        font-size: 110%;
    }
    .item-bottom button a{
        padding: 5% 1%;
    }
    .item img{
        max-height: 180px;

    }
}

@media(max-width:800px){
    .item{
        padding: 1%;
        min-width: 20vw;
    }
    .product_name{
        font-size: 125%;
        padding: 0px;
    }
    .old_price{
        font-size: 80%;
    }
    .new_price{
        font-size: 110%;
    }
    .item-bottom button a {
        font-size: 75%;
        padding: 10px 3px;
    }
}

@media(max-width:550px){
    .item{
        min-width: 35vw;
    }
}
