.single-blog-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.single-blogitem{
    margin: 3% 0%;
    width: 60%;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    gap: 20px;
}

.single-blog-container img{
    width: 100%;
    object-fit: cover;
    border-radius: 5px;
}

@media(max-width:800px){
    .single-blogitem{
        width: 80%;
    }
    .single-blogitem>div{
        font-size: 16px;
    }
}