.footer{
    display: flex;
    justify-content: space-around;
    color: white;
    background-color: #ef4242;
    padding: 2%;
    gap: 7%;
    box-shadow: inset 0px 7px 10px -10px rgb(0 0 0);
}
.footer-right{
    display: flex;
    gap: 20px;
    justify-content: space-around;
}
.footer li{
    list-style-type: none;
}
.footer a{
    text-decoration: none;
    color: rgb(227, 227, 227);
    transition: all 100ms;
}
.footer a:hover{
    color: white;
}

.follow{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.follow ul{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.follow a{
    display: flex;
}

@media(max-width:1024px){
    .info{
        font-size: 13px;
    }
}

@media(max-width:800px){
    .footer{
        gap: 1%;
    }
    .info{
        font-size: 13px;
    }
}

@media(max-width:550px){
    .footer{
        flex-direction: column;
        gap: 10px;
    }

    .info{
        font-size: 10px;
    }
}