
.content-container{
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 20px 50px;
}

.content-box{
    background-image: linear-gradient(to bottom right, rgb(251, 251, 251), rgb(255, 255, 255));
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.content-box h2{
    margin-bottom: 20px;
}
.content-box p{
    margin-bottom: 5px;
}

.box1{
    display: flex;
}
.box1 img{
    width: 30%;
    height: 30%;
}

@media(max-width:550px){
    .content-container{
        padding: 10px;
    }
}