.blog-item{
    width: 100%;
    display: flex;
    flex-direction: column;
    transition: all 200ms;
    padding: 1%;
    border-radius: 3px;
    cursor: pointer;
}
.blog-item a{
 text-decoration: none;
 color: black;
}
.blog-item:hover{
    box-shadow: 0px 0px 10px 0px rgb(0 0 0);

}
.blog-item img{
    width: 100%;
    height: 10rem;
    object-fit: cover;
    align-self: center;
    border-radius: 5px;
}

.blog-item-bottom{
    padding-right: 20px;
}
.blog-item-bottom p{
    font-weight: bold;
}

#top-blog{
    height: 24rem;
}

@media(max-width:800px){
    #top-blog{
        height: 15rem;
    }
    .blog-item img{
        height: 7rem;
    }
    .blog-item-bottom{
        padding: 0px;
    }
    .blog-item-bottom p{
        font-size: 13px;
    }
}
@media(max-width:550px){
    .blog-item img{
        height: 6rem;
    }
    #top-blog{
        height: 13rem;
    }
}

